import React, {useEffect} from "react"
import Profile from "./profile"
import {connect} from "react-redux"
import {
    getProfileUser,
    getStatus,
    updateStatus,
    uploadPhoto,
} from "../../../Redux/profileReducer"
import {withRouter} from "react-router-dom";
import {WithAuthRedirect} from "../HOC/WithAuthRedirect";
import {compose} from "redux";

const ProfileContainerHook = ({getStatus,getProfileUser,...props})=> {
    let UrlId = props.match.params.userId;
    if (!UrlId) {UrlId = props.authId}

    useEffect(()=>{
        getProfileUser(UrlId)
        getStatus(UrlId)
    },[getProfileUser,getStatus,UrlId])

    return <Profile {...props}/>
}

let mapStateToProps = (state) => ({
    profile: state.ProfilePage.Profile,
    status: state.ProfilePage.Status,
    authId: state.auth.id,
    id: state.ProfilePage.id,
    StatusUpdateMessage: state.ProfilePage.StatusUpdateMessage
})

export default compose(WithAuthRedirect,withRouter,connect(mapStateToProps,{uploadPhoto,getProfileUser,getStatus,updateStatus})

)(ProfileContainerHook);
