import userPhoto from "../../../Users/img/user.png";
import style from "../../profile.module.css";
import React from "react";

const EditProfileImage = (props) => {

    const mainPhotoSelected = (el) => {
        props.uploadPhoto(el.target.files[0]);
    };

    return (
        <div className={style.imageBlock}>
            <img src={props.profile.photos.small != null ?
                props.profile.photos.small : userPhoto} alt='logo' className={style.profileImg}/>
            <div>
                {(props.userId === props.authId) && (false) &&
                <input type={'file'} onChange={mainPhotoSelected}/>}
            </div>
        </div>
    )
}

export default EditProfileImage;